import Bus from '../eventbus'
export default {
  name: 'cc',
  // props 
  props: ['time'],
  data () {
    return {
      msg: 'Welcome to cc',
      // 
      cctime: this.time,
      tocc: 'cc'
    }
  },
  methods: {
    // 
    uo2baba () {
      this.$emit('cc-son', '10000');
    },
    //  bbb (eventbus)
    tobbb () {
      Bus.$emit('fromcc', this.tocc);
    }
  },
  beforeDestroy () {
    // created
    this.tobbb();

  },
  destroyed () {
    // 
    Bus.$off('fromcc', this.tocc);
  }
} 




