

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import Vux from './components/HelloFromVux'
import aac from './Components/aac'
import cc from './Components/cc'
import bbb from './Components/bbb'
import list_state from './Components/list_state'
import list_detail from './Components/list_detail'

import Bus from './eventbus'

import axios from 'axios'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

Vue.prototype.$ajax = axios
Vue.component('x-button', XButton)
Vue.component('toast', Toast)

Vue.use(VueRouter)
Vue.use(LoadingPlugin)

const routes = [
{
path: '/',
name: 'Vux',
component: Vux
},
{
path: '/bbb',
name: 'bbb',
component: bbb
},
{
path: '/aac',
name: 'aac',
component: aac,
children: [
{
path: 'cc',
name: 'cc',
component: cc
}
]
},
{	// aac
path: '/aac/list_state',
component: list_state,
},
{
path: '/list_state/list_detail',
component: list_detail
}
]
const router = new VueRouter({
routes
})

FastClick.attach(document.body)

Vue.config.devtools = true; 
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router,
created () {
this.$vux.loading.show({
text: '...'
})
},
mounted () {
setTimeout(() => {
this.$vux.loading.hide();
},1000)
},
render: h => h(App)
}).$mount('#app-box')



