import Bus from '../eventbus'
import upimg from '@/components/upimg'
import cc from '@/Components/cc'

export default {
  name: 'aac',
  components: {
    cc,
    upimg
  },
  data () {
    return {
      msg: 'Welcome to aac',
      time: 'aac ' + new Date(),
      name: 'haha',
      upnum: 3, // 
    }
  },
  methods: {
    // 
    back () {
      history.back();
    },
    //  cc emit
    receiveFromCc (data) {
      console.log('ccData: ',data);
    },
    getupimg (img) {
      // img
      console.log('img: ',img)
    }
  },
  created () {
    // console.log('router: ',this.$router);
    // upimg $emitupimg
    Bus.$on('upimg', this.getupimg);
  },
  destroyed (){
    // 
    Bus.$off('upimg',this.getupimg);
  }
}



