import Bus from '../eventbus'
export default {
  name: 'BBB',
  data () {
    return {
      msg: 'Welcome to Bbb',
      ccdata: '',   // cceventbus
    }
  },
  created () {
    //  cc (eventbus)
    Bus.$on('fromcc', this.getcc);
  },
  methods: {
    getcc (val) {
      console.log('val: ', val);
      this.ccdata = val;
    }
  },
  beforeDestroy () {
    // 
    Bus.$off('fromcc', this.getcc);
  }
}




