 /**
 * [upFile ]
 * @param {[type]} [formdata] [formdata]
 * @param {[type]} [input] [input type="file"]
 * @param {[function]} [comfn] []
 * @param {[function]} [callback] []
 */
function upImg( input, COMFN, CALLBACK){
	// formdata = formdata ? formdata : new FormData();
	var backdata = {};
	/*
		
	 */
	input.onchange = function(e){
		var fileList = e.target.files;
		console.log(' ',fileList)
		if(fileList.length == 1){
			// 
			var reader = new FileReader();
			// 
			reader.readAsDataURL(fileList[0]);
			reader.onload = function(e){
				// console.log('e1: ',e);
				readerFun(e, this);
			}
		}else if(fileList.length >= 2){
			//  input multiple
			// PCchromeuc, ios
			//multiple 
			// console.log(Object.prototype.toString.call(fileList))
			for(var i in fileList){
				// console.log(Object.prototype.toString.call(fileList[i]));
				if(Object.prototype.toString.call(fileList[i]) === '[object File]'){
					var reader = new FileReader();
					(function(file){

						reader.readAsDataURL(file);
						reader.onload = function(e){
							// console.log('e2: ',e);
							readerFun(e, this);
						}
					})(fileList[i]);
				}
			}
		}
	}

	/**
	 * 
	 * @param  {[type]} e [description]
	 * @return {[type]}   [description]
	 */
	function readerFun(e, file_reader){
		var dx = (e.total/1024)/1024;
		var result = file_reader.result;
		// console.log('e: ',e);
		if(dx > 6){
			backdata['err'] = '6M6M';
			// 
			CALLBACK(backdata);
			return
		}
		if(dx >= 2){
			console.log("2M");
			//2M
			compressImage(result, function(res){
				backdata['compress'] = true;
				//base642
				if(res.src.length <= 6){
					// toDataURL  data:,
					res.src = result;
					backdata['compress'] = false;
				}
				backdata['src'] = res.src;
				convert2Binary(res.src, function(bin){
					backdata['binsrc'] = bin.src;
					// 
					CALLBACK(backdata);
				})
			});
			
		}else{
			backdata['src'] = result;
			backdata['compress'] = false;
			// 
			convert2Binary(result, function(bin){
				backdata['binsrc'] = bin.src;
				// 
				CALLBACK(backdata);
			})
			// 
			// CALLBACK(backdata);
			// console.log(' ',formdata.getAll('UploadForm[]'))
		}

	}

	/*
		(canvas)
	*/
	var canvas = document.createElement("canvas");
	var ctx = canvas.getContext("2d");
	var img = new Image();
	function compressImage(_url, fallback) {
		// 
		COMFN();
		img.src = _url;
		img.onload = function(){
			canvas.width = img.width;
			canvas.height = img.height;
			setTimeout(function(){
				ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
				var src = canvas.toDataURL('image/jpeg', .6);
				// console.log(src);
				// src
				fallback({src: src});
				
			},100);
		}
		
	}
	/*
		base642
	 */
	function convert2Binary(dataURI, fallback){
		var byte_str = window.atob(dataURI.split(',')[1]);
		var ab = new ArrayBuffer(byte_str.length);
		var ua = new Uint8Array(ab);

		for(var i=0; i<byte_str.length; i++){
			ua[i] = byte_str.charCodeAt(i)
		}
		// console.log(ua)
		var bb = new window.Blob([ ab ]);
		// console.log(bb)
		// 
		fallback({src: bb});
	}

}
export { upImg }


